import * as d3 from "d3";
import { svg } from "d3";
/**
 * 
 * @param {HTMLDivElement} container 
 * @param {number} width 
 * @param {number} height 
 * @param {(x: number, y:number) => number} func
 * @param {[number, number]} xDomain
 * @param {[number, number]} yDomain
 */
export function drawFunctionGraph(container, width, height, func, xDomain, yDomain, extent){
    const root = d3.select(container)
        .append("svg")
        .attr("width", width)
        .attr("height", height)
        .attr("viewbox", `0 0 ${width} ${height}`)
    
    const margin = {top:50, right:50, bottom:50, left:50};

    const scaleX = d3.scaleLinear().domain(xDomain).range([margin.left, width-margin.right]);
    const scaleY = d3.scaleLinear().domain(yDomain).range([height-margin.bottom, margin.top]);

    const scaleColor = d3.scaleDiverging().domain([extent[0], (extent[0]+extent[1])/2 , extent[1]]).interpolator(d3.interpolateRdYlBu);

    const axisX = d3.axisBottom(scaleX).ticks(5);
    const axisY = d3.axisLeft(scaleY).ticks(5);

    const groupMain = root.append("g");
    const groupX = root.append("g").attr("transform", `translate(${0}, ${width - margin.bottom})`);
    const groupY = root.append("g").attr("transform", `translate(${margin.left}, ${0})`);

    axisX(groupX);
    axisY(groupY);

    //const data = [];
    for(let row = height - margin.bottom; row>margin.top; --row){
        //const dataPerRow = [];
        for(let col = margin.left; col<width-margin.right; ++col){
            const val = func(scaleX.invert(col), scaleY.invert(row));
            groupMain.append("rect")
                .attr("x", col)
                .attr("y", row)
                .attr("width", 1)
                .attr("height", 1)
                .attr("fill", scaleColor(val))
        }
        //data.push(dataPerRow);
    }

    // for(let i=0; i<data.length; ++i){
    //     for(let j=0; j<data.length; ++j){
    //         root.append("rect")
    //             .attr("x", scaleX(j))
    //             .attr("y", scaleY(i))
    //             .attr("width", 1)
    //             .attr("width", 1)
    //     }
    // }
    drawGradient(root, height, width, {top:50, right:30, bottom: 50, left:160}, scaleColor);
}

function drawGradient(root, height, width, margin, scaleColor, vertical=true, reverse=false) {
    const domain = scaleColor.domain();

    const graidentID = "gradient";
    const gradient = root
      .append("defs")
      .append("linearGradient")
      .attr("id", graidentID);

    if (vertical) {
      gradient.attr("x1", 0).attr("x2", 0).attr("y1", 0).attr("y2", 1);
    }

    let newDomain = domain.reverse();
    console.log(newDomain);
    if (!reverse) newDomain = domain.reverse();
    newDomain.forEach((value, i) => {
      gradient
        .append("stop")
        .attr("offset", `${(i / (domain.length - 1)) * 100}%`)
        .attr("stop-color", scaleColor(value));
    });

    root
      .append("rect")
      .attr("x", margin.left)
      .attr("y", margin.top)
      .attr("width", width - margin.left - margin.right)
      .attr("height", height - margin.top - margin.bottom)
      .attr("fill", `url(#${graidentID})`);


    const groupY = root.append("g").attr("transform", `translate(${width-margin.right}, ${0})`);
    const scale = d3.scaleLinear().domain([domain[0], domain[2]]).range([height-margin.bottom, margin.top]);
    const axis = d3.axisRight(scale).ticks(5);
    axis(groupY);
  }